.wrapper {
    display: flex;
    justify-content: center;
    padding-top: 70px;
    padding-bottom: 50px;
    color: var(--text);
    /* background-color: black; */
  }

  .content_wrapper {
    width: 95%;
    max-width: 1000px;
    padding: 20px;
    background-color: var(--base_light);
    border: 1px solid var(--main);
    border-radius: 5px;
  }

  .content_wrapper h1 {
    margin: 20px 0 30px 0;
    font-size: 48px;
    font-weight: 800;
    color: var(--main);
  }
  
  .content_wrapper h2 {
    margin: 20px 0 10px 0;
    font-size: 24px;
    font-weight: 400;
    color: var(--acc);
  }
  
  .content_wrapper p {
    margin-bottom: 5px;
    font-weight: 300;
  }
  
  .content_wrapper a {
    color: var(--main_light);
  }
  
  .content_wrapper a:hover {
    color: var(--text);
  }

  .img_container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    overflow: hidden;
    padding-top: 10px;
  }
  
  .img_container img {
    max-width: 400px;
    min-width: 240px;
    width: 48%;
    margin-bottom: 10px;
    border: 1px solid var(--main);
    background-color: var(--text);
  }